import { Language } from '/@/locales'
import { ExtraKeyValue } from './extra'
import { Category } from './category'
import { Tag } from './tag'

export enum ArticleOrigin {
  Original = 0, // 
  Reprint = 1, // 
  Hybrid = 2 // 
}

export enum ArticleLanguage {
  Chinese = 'zh', // 
  English = 'en', // English
  Multiple = 'mul' // 
}

export const ArticleLanguageI18n: Record<ArticleLanguage, Record<Language, string>> = {
  [ArticleLanguage.Chinese]: {
    [Language.Chinese]: '',
    [Language.English]: 'ZH'
  },
  [ArticleLanguage.English]: {
    [Language.Chinese]: '',
    [Language.English]: 'EN'
  },
  [ArticleLanguage.Multiple]: {
    [Language.Chinese]: '',
    [Language.English]: 'MUL'
  }
}

export interface Article {
  id: number
  title: string
  summary: string
  keywords: string[]
  content: string
  thumbnail: string
  origin: ArticleOrigin
  lang: ArticleLanguage
  featured: boolean
  disabled_comments: boolean
  tags: Tag[]
  categories: Category[]
  stats: {
    likes: number
    views: number
    comments: number
  }
  extras: ExtraKeyValue[]
  updated_at: string
  created_at: string
}
