  <div class="page-banner">
    <div class="background">
      <video
        class="video"
        loop
        muted
        autoplay
        :controls="false"
        :src="getAssetURL(cdnDomain, '/videos/clips/ocean-6.mp4')"
      ></video>
    </div>
    <div class="content">
      <div class="fullwidth">
        <div class="profile">
          <div class="avatar">
            <uimage class="image" :src="useAdminAvatar(adminProfileStore.data?.avatar_url)" />
          </div>
          <div class="infos">
            <h1 class="name">{{ adminProfileStore.data?.name || '-' }}</h1>
            <h4 class="slogan">{{ adminProfileStore.data?.slogan || '-' }}</h4>
          </div>
        </div>
        <h3 class="description">
          <webfont bolder>{{ isZhLang ? APP_PROFILE.description_zh : APP_PROFILE.description_en }}</webfont>
        </h3>
        <div class="socials">
          <ulink class="item icon-only instagram" title="Instagram" :href="goLinks.instagram">
            <i class="iconfont icon-instagram" />
          </ulink>
          <ulink class="item icon-only threads" title="Threads" :href="goLinks.threads">
            <i class="iconfont icon-threads" />
          </ulink>
          <ulink class="item with-text github" :href="goLinks.github">
            <i class="iconfont icon-github" />
            <span class="text">GitHub</span>
          </ulink>
          <ulink class="item icon-only youtube" title="YouTube" :href="goLinks.youtube">
            <i class="iconfont icon-youtube" />
          </ulink>
          <ulink class="item icon-only telegram" title="Telegram" :href="goLinks.telegram">
            <i class="iconfont icon-telegram" />
          </ulink>
          <button class="item icon-only wechat" title="WeChat" @click="handleOpenWeChatModal">
            <i class="iconfont icon-wechat" />
            <client-only>
              <popup v-model:visible="wechatModalOpened" :body-scrollable="false">
                <div class="qrcode-modal wechat">
                  <div class="background"></div>
                  <uimage class="image" cdn src="/images/qrcodes/wechat.webp" />
                  <span class="text">
                    <i18n>
                      <template #en>Friend me on WeChat</template>
                      <template #zh>  </template>
                    </i18n>
                  </span>
                </div>
              </popup>
            </client-only>
          </button>
          <ulink class="item icon-only linkedin" title="LinkedIn" :href="goLinks.linkedin">
            <i class="iconfont icon-linkedin" />
          </ulink>
          <ulink class="item icon-only zhihu" title="" :href="goLinks.zhihu">
            <i class="iconfont icon-zhihu" />
          </ulink>
          <ulink class="item icon-only douban" title="" :href="goLinks.douban">
            <i class="iconfont icon-douban" />
          </ulink>
          <ulink class="item icon-only email" title="Email me" :href="emailLink">
            <i class="iconfont icon-mail" />
          </ulink>
        </div>
      </div>
      <div class="container">
        <div
          class="biography"
          :class="
            isZhLang
              ? 'zh'
              : globalState.userAgent.isFirefox || globalState.userAgent.isSafari
                ? 'en-hack'
                : 'en'
          "
          v-html="
            markdownToHTML((isZhLang ? appConfig.ABOUT_BIOGRAPHY_ZH : appConfig.ABOUT_BIOGRAPHY_EN) ?? '', {
              sanitize: false
            })
          "
        ></div>
      </div>
    </div>
  </div>
